.navbar{
    display:flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px rgb(100, 57, 110);
}

.nav-logo{
    display: flex;
    align-items: center;
    gap:10px;
}

.nav-logo p{
    color: rgb(79, 41, 125);
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display:flex;
    align-items: center;
    list-style: none;
    gap:50px;
    color:grey;
    font-size:20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:3px;
    cursor: pointer;
}



.nav-menu hr{
    border:none;
    width:80%;
    height: 3px;
    border-radius: 10px;
    background: rgb(219, 175, 229);
}

.nav-login-cart{
    display: flex;
    align-items: center;
    gap:45px;
}

.nav-login-cart button{
    width:157px;
    height: 58px;
    outline:none;
    border:1px solid grey;
    border-radius: 75px;
    color: gray;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.nav-login-cart button:active{
    background: rgb(229, 179, 245);
}

.nav-cart-count{
    width:22px;
    height:22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -35px;
    border-radius:11px;
    font-size: 14px;
    background: rgb(123, 25, 165);
    color: white;
}